import './AdminFabricante.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page, SDataTable } from '../../components'
import { RUTAS } from '../../constants'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

export const AdminFabricante: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { adminStore, loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const DataTable = React.createRef<any>()
  const Estado = history.location.pathname.split('/')[3]
  const addToast = useToaster()

  React.useEffect(() => {
    DataTable.current.refresh()
  }, [window.location.pathname])

  const setup = () => {
    obtenerRubros()
    rootStore.setModeradoPendiente(Estado)
  }

  useEffect(() => {
    setup()
  }, [])

  const visualizar = (row: any) => {
    history.push(RUTAS.ADMIN_FABRICANTE_DETAIL(Estado, row.id))
  }

  const editar = (row: any) => {
    history.push(RUTAS.ADMIN_FABRICANTE_UPDATE(Estado, row.id))
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea ejecutar la acción?')) {
      return false
    }
    const accion = row.aprobado ? (row.bloqueado ? 'Desbloqueado' : 'Bloqueado') : 'Eliminado'
    const dtable = DataTable.current
    rootStore.environment.api.eliminarFabricante(row.id).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast(accion + ' satisfactoriamente', 'success')
        dtable.refresh()
      } else {
        addToast('Ocurrió un error al ejecutar la acción, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const obtenerRubros = () => {
    rootStore.environment.api.rubros().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setRubros(result.rubros)
      }
    })
  }

  const renderAcciones = (row: any) => {
    const iconoEliminar = row.aprobado ? (
      row.bloqueado ? (
        <FIcon.Check size={14} />
      ) : (
        <FIcon.Slash size={14} />
      )
    ) : (
      <FIcon.Trash size={14} />
    )
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
        <span className={'button ml-3'} title={'Editar'} onClick={() => editar(row)}>
          <FIcon.Edit size={14} />
        </span>
        {loginStore.administrador && (
          <span
            className={'button ml-3'}
            title={row.aprobado ? (row.bloqueado ? 'Desbloquear' : 'Bloquear') : 'Eliminar'}
            onClick={() => eliminar(row)}
          >
            {iconoEliminar}
          </span>
        )}
      </div>
    )
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Proveedores</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Proveedores</li>
              </ol>
            </div>
          </div>
          {loginStore.administrador && Estado === 'pendiente' && (
            <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
              <li className="nav-item ml-auto">
                <button
                  className="btn btn-primary d-flex align-items-center px-3"
                  id="add-fabricante"
                  onClick={() => {
                    adminStore.setFabricanteDetalle(null)
                    history.push(RUTAS.ADMIN_FABRICANTE_CREATE)
                  }}
                >
                  Crear Proveedor
                </button>
              </li>
            </ul>
          )}
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  let moderado = history.location.pathname.split('/')[3] !== 'pendiente'
                  return rootStore.environment.api.fabricantes(page, perPage, order, search, moderado)
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Imagen',
                    selector: 'imagen_perfil',
                    sortable: false,
                    cell: (row: any) =>
                      row.imagen_perfil ? (
                        <img
                          src={row.imagen_perfil}
                          style={{ maxWidth: 50, maxHeight: 50, borderRadius: 10, background: '#ededed' }}
                        />
                      ) : (
                        ''
                      ),
                  },
                  {
                    name: 'Nombre',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'Aprobado',
                    selector: 'aprobado',
                    sortable: true,
                    cell: (row: any) => (row.aprobado ? 'Sí' : 'No'),
                  },
                  {
                    name: 'Bloqueado',
                    selector: 'bloqueado',
                    sortable: true,
                    cell: (row: any) => (row.bloqueado ? 'Sí' : 'No'),
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminFabricante
